import React, { Component } from 'react';
import AddTodo from './AddTodo';
import Filter from './Filter';
import Todos from './Todos';

export class Home extends Component {
  state = {
    todos: [
      { task: 'Listen Music', id: 1, done: false },
      { task: 'Watch Movie', id: 2, done: false },
      { task: 'Learn React', id: 3, done: false }
    ],
    filter: ''
  }

  addTodo = (todo) => {
    const newTodo = { task: todo, id: this.findMaxId() + 1, done: false };

    this.setState({ todos: [...this.state.todos, newTodo] });
  }

  markTodo = (id) => {
    const copyTodos = [...this.state.todos];
    for (const todo of copyTodos) {
      if (todo.id === id) {
        todo.done = !todo.done;
      }
    }

    this.setState({ todos: copyTodos });
  }

  removeTodo = (id) => {
    let todos = this.state.todos.filter((todo) => (
      todo.id !== id
    ));

    todos = todos.map((todo) => {
      if (todo.id >= id) {
        todo.id--;
      }
      return todo;
    });

    this.setState({ todos: todos });
  }

  filterTodo = (content) => {
    this.setState({filter: content})
  }

  findMaxId = () => {
    let lastTodo;
    try {
      lastTodo = this.state.todos.reduce((max, current) => (
        current.id > max.id ? current : max
      ));
    } catch (e) {
      return 0;
    }

    return lastTodo.id;
  }

  render() {
    return (
      <div className="container pt-5">
         <Filter filterTodo={this.filterTodo}/>
          <Todos todos={this.state.todos} removeTodo={this.removeTodo} markTodo={this.markTodo} filterKeyword={this.state.filter} />
          <AddTodo addTodo={this.addTodo} />
      </div>
    )
  }
}