import React, { Component } from 'react';

class Action extends Component {
  state = {
    done: false
  }

  render() {
    return (
      <div className="action d-flex">
          <div className="checkbox-container mr-2">
            <label className="checkbox-label">
              <input type="checkbox" onChange={(e) => { this.props.markTodo(this.props.id) }}/>
                <span className="checkbox-custom rectangular"></span>
            </label>
          </div>
          <button className="remove-task btn btn-danger btn-sm py-0" onClick={(e) => { this.props.removeTodo(this.props.id); console.log(this.props.id);}}>&times;</button>
        </div>
    )
  }
};

export default Action;