import React from 'react';
import './Todos.css';
import Action from './Action';

const Todos = ({ todos, removeTodo, markTodo, filterKeyword }) => {
  let todosList;
  if (todos.length === 0) {
    todosList = <div className="alert alert-success text-center">
      congratulations! <strong>All tasks</strong> are done.
    </div>
  } else {
    todosList = todos.map((todo) => {
      if (todo.task.toLowerCase().indexOf(filterKeyword) !== -1) {
        return <div className="list-group-item d-flex justify-content-between align-items-center" key={todo.id}>
          <div className="task">
            <span className={todo.done ? 'deleted' : ''}>{todo.task}</span>
          </div>
          <Action id={todo.id} removeTodo={removeTodo} markTodo={markTodo} />
        </div>;
      } else {
        return null;
      }
    });
  }

  const itIsEmptyMessage = <div className="alert alert-info">There is no<strong>Todo</strong> with {filterKeyword} keyword here!</div>;

  return (
    <div className="todos">
      <div className="list-group">
  {todosList.every(todo => todo === null) ? (itIsEmptyMessage) : todosList}
      </div>
    </div>
  );
}


export default Todos;